export default "import {\n  ContourViewer,\n  CornerstoneImage,\n  CornerstoneSingleImage,\n  InsightViewer,\n  InsightViewerContainer,\n  installWADOImageLoader,\n  unloadImage,\n  useContour,\n  useInsightViewerSync,\n} from '@lunit/insight-viewer';\nimport React, { useMemo, useState } from 'react';\nimport { initialContours } from '../../../__fixtures__/contour';\n\ninstallWADOImageLoader();\n\nconst width: number = 400;\nconst height: number = 500;\n\nexport default () => {\n  const image: CornerstoneImage = useMemo(\n    () =>\n      new CornerstoneSingleImage(`wadouri:https://fixtures.front.lunit.io/dcm-files/series/CT000010.dcm`, {\n        unload: unloadImage,\n      }),\n    [],\n  );\n\n  const [divElement, setDivElement] = useState<HTMLDivElement | null>(null);\n\n  const { cornerstoneRenderData, updateCornerstoneRenderData } = useInsightViewerSync();\n\n  const { contours, focusedContour } = useContour({\n    mode: 'contour',\n    initialContours,\n  });\n\n  return (\n    <InsightViewerContainer ref={setDivElement} width={width} height={height}>\n      <InsightViewer\n        width={width}\n        height={height}\n        invert={false}\n        flip={false}\n        pan={divElement}\n        adjust={false}\n        zoom={divElement}\n        resetTime={0}\n        image={image}\n        updateCornerstoneRenderData={updateCornerstoneRenderData}\n      />\n      {contours && contours.length > 0 && cornerstoneRenderData && (\n        <ContourViewer\n          width={width}\n          height={height}\n          contours={contours}\n          focusedContour={focusedContour}\n          cornerstoneRenderData={cornerstoneRenderData}\n        />\n      )}\n    </InsightViewerContainer>\n  );\n};\n"